/*
    To render a video with ffmpeg, move the output pngs to the input folder of this repo and run
    ffmpeg -framerate 10 -i input/artarm_3124661_output_%0d.png output/output_10.mp4

    Change the framerate to achieve different levels of timelapse
*/

/*
    IDEA LIST
        - music visualiser
        - music visualisation on splines?
        - simple draw mode
        - text (text masks? Draw with letters?)
*/

import * as dat from './_snowpack/pkg/dat.gui.js';

export const sketch = (_settings, callback, p) => {
    const { Bodies, Body, Composite, Composites, Constraint, Engine, Mouse, MouseConstraint, Render, Runner, World } = Matter;
    const gui = new Tweakpane.Pane({container: document.getElementById("menu")});
    const menu = document.getElementById("menu");
    const seed = Math.round(Math.random() * 10000000);
    let color;
    let settings;
    let bgImg;
    let shoulderJoint;
    let x, sampleX;
    let y, sampleY;
    let masked = false;
    let arm;
    let frame = 0;
    let framesSinceLastDraw = 0;
    let _brushSize;
    let sizeRatio = 1;
    let previousFrame = 0;
    let framesBetweenCaptures = Math.infinity;
    let resetArm;
    let numBrushTypes = 8;
    let font1, font2, font3, font4, font5;
    let fonts;
    let engine;
    let perlinIndexX = 1;
    let perlinIndexY = 1;
    let previousBrushLocation = [];
    //recording vars
    let mediaRecorder;
    let chunks = [];

    p.preload = function() {
        bgImg = p.loadImage(`https://picsum.photos/${window.innerWidth}/${window.innerHeight}`);
        // bgImg = p.loadImage(`assets/astri.jpg`);
        font1 = p.loadFont("assets/fonts/EastSeaDokdo-Regular.ttf");
        font2 = p.loadFont("assets/fonts/Oswald-VariableFont_wght.ttf");
        font3 = p.loadFont("assets/fonts/PirataOne-Regular.ttf");
        font4 = p.loadFont("assets/fonts/PlayfairDisplay-Italic-VariableFont_wght.ttf");
        font5 = p.loadFont("assets/fonts/PlayfairDisplay-VariableFont_wght.ttf");
    }

    function loadNewImage() {
        if(confirm("Do you want to load a new random image to use as the basis for your piece?")) {
            p.loadImage(`https://picsum.photos/${window.innerWidth}/${window.innerHeight}`, img => {
                bgImg = img;
                bgImg.loadPixels();

                //figure out if we need to scale the image
                let sizeXRatio = bgImg.width / window.innerWidth;
                let sizeYRatio = bgImg.height / window.innerHeight;
                sizeRatio = Math.max(sizeXRatio, sizeYRatio);
            });
        }
    }

    function randomBrush() {
        settings.brushAlpha = Math.random() * 255;
        settings.brushSize = Math.random() * 99 + 1;
        settings.brushType = Math.floor(Math.random() * (numBrushTypes + 0.99999));
        settings.jointsOnArm = Math.random() * 24 + 1;
        settings.jointLength = Math.random() * 249 + 1;
        settings.rotateBrush = Math.random() < 0.5;
        gui.refresh();
        resetArm();
    }

    function save() {
        p.save();
    }

    function clearCanvas() {
        if(confirm("Do you want to clear the canvas?")) {
            p.clear();
            p.background([settings.background.r, settings.background.g, settings.background.b, settings.background.a * 255]);
        }
    }

    p.setup = function() {
        p.createCanvas(window.innerWidth, window.innerHeight);
        bgImg.loadPixels();

        fonts = {"East Sea Dokdo": font1, "Oswald": font2, "Pirata One": font3, "Playfair Display Italic": font4, "Playfair Display": font5};

        //figure out if we need to scale the image
        let sizeXRatio = bgImg.width / window.innerWidth;
        let sizeYRatio = bgImg.height / window.innerHeight;
        sizeRatio = Math.max(sizeXRatio, sizeYRatio);

        //wrapping this to be able to collapse it in IDE
        function populateSettings() {
            settings = {
                "brushSize" : _settings.brushSize || 50,
                "randomForceFactor" : _settings.randomForceFactor || 0,
                "brushAlpha" : _settings.brushAlpha || 66,
                "rotateBrush" : _settings.rotateBrush || true,
                "endJointDensity" : _settings.endJointDensity || 0.001,
                "jointsOnArm": _settings.jointsOnArm || 7,
                "jointLength": _settings.jointLength || 1,
                "simulationSpeed": _settings.simulationSpeed || 1,
                "brushType": _settings.brushType || 2,
                "armStiffness": _settings.armStiffness || 0.4,
                "originX": _settings.originX || p.width / 2,
                "originY": _settings.originY || p.height / 2,
                "background": _settings.background || {r: 20, g: 20, b: 30, a: 1},
                "enableTint": _settings.enableTint || false,
                "fade": _settings.fade || false,
                "fadeAmount": _settings.fadeAmount || 50,
                "continousDrawing": _settings.continousDrawing || false,
                "modulateBrushSize": _settings.modulateBrushSize || true,
                "modulateOrigin": _settings.modulateOrigin || false,
                "followMouse": _settings.followMouse || true,
                "perlinWalk": _settings.perlinWalk || false,
                "perlinFactor": _settings.perlinFactor || 0.01,
                "modulationXSpeed": _settings.modulationXSpeed || 0.1,
                "modulationYSpeed": _settings.modulationYSpeed || 0.1,
                "modulateOriginXFactor": _settings.modulateOriginXFactor || 0.1,
                "modulateOriginYFactor": _settings.modulateOriginYFactor || 0.1,
                "modulationSpeed": _settings.modulationSpeed || 1,
                "tint": _settings.tint || {r: 127, g: 127, b: 127},
                "singleColor": _settings.singleColor || true,
                "selectedColor": _settings.selectedColor || {r: 150, g: 150, b: 200},
                "colorWobble": _settings.colorWobble || true,
                "wobbleSpeed": _settings.wobbleSpeed || 0.75,
                "wobbleRange": _settings.wobbleRange || 20,
                "capturing": _settings.capturing || false,
                "captureFramerate": _settings.captureFramerate || 30,
                "framesBetweenDraws": _settings.framesBetweenDraws || 0,
                "useTextMask": _settings.useTextMask || false,
                "useImageMask": _settings.useImageMask || false,
                "textMask": _settings.textMask || "VIRTUAL BEASTS",
                "font": _settings.font || "Oswald",
                "fontSize": _settings.fontSize || 90
            }
            _brushSize = settings.brushSize;
    
            let brushFolder = gui.addFolder({title: "Brush", expanded: false});
            brushFolder.addInput(settings, "brushAlpha", {min: 0.01, max: 255, step: 0.1, label: "Brush Alpha"});
            brushFolder.addInput(settings, "brushSize", {min: 1, max: 100, step: .1, label: "Brush Size"}).on("change", (ev) => {
                _brushSize = ev.value;
            });

            brushFolder.addInput(settings, "brushType", {min: 0, max: numBrushTypes, step:1, label: "Brush Shape"});
            brushFolder.addInput(settings, "rotateBrush", {label: "Rotate Brush"});
            brushFolder.addInput(settings, "modulateBrushSize", {label: "Modulate Brush Size"}).on("change", (ev) => {
                if (!ev.value) {
                    settings.brushSize = _brushSize;
                }
            });
            brushFolder.addInput(settings, "modulationSpeed", {min: 0.01, max: 4, step: 0.01, label: "Size Modulation Speed"});
            brushFolder.addInput(settings, "fade", {label: "Ephemeral Paint"});
            brushFolder.addInput(settings, "fadeAmount", {min: 0.1, max: 100, step: 0.1, label: "Paint Fade Speed"});

            let armFolder = gui.addFolder({title: "Arm", expanded: false});
            armFolder.addInput(settings, "jointsOnArm", {min: 1, max: 25, step: 1, label: "Joints on Arm"}).on("change", () => resetArm());
            armFolder.addInput(settings, "jointLength", {min: 1, max: 250, step: 1, label: "Arm Joint Length"}).on("change", () => resetArm());
            armFolder.addInput(settings, "originX", {min: 1, max: p.width, step: 1, label: "Arm Origin X"}).on("change", (ev) => resetArm());
            armFolder.addInput(settings, "originY", {min: 1, max: p.height, step: 1, label: "Arm Origin Y"}).on("change", (ev) => resetArm());
            armFolder.addInput(settings, "armStiffness", {min: 0, max: 1, step: 0.01, label: "Arm Stiffness"}).on("change", (ev) => resetArm());
            armFolder.addInput(settings, "endJointDensity", {min: 0.001, max: 1, step: 0.001, label: "End Joint Density"}).on("change", (ev) => {
                Body.setDensity(arm.bodies[arm.bodies.length - 1], ev.value);
            });
    
            let colorFolder = gui.addFolder({title: "Color", expanded: false});
            colorFolder.addInput(settings, "background", {label: "Background Color"}).on("change", (ev) => {
                p.clear();
                p.background([ev.value.r, ev.value.g, ev.value.b, ev.value.a * 255]);
            });
            colorFolder.addInput(settings, "tint", {label: "Brush Tint"});
            colorFolder.addInput(settings, "enableTint", {label: "Enable Tint"});
            colorFolder.addInput(settings, "singleColor", {label: "Enable Single Color"});
            colorFolder.addInput(settings, "selectedColor", {label: "Single Color"});
            colorFolder.addInput(settings, "colorWobble", {label: "Enable Color Wobble"});
            colorFolder.addInput(settings, "wobbleSpeed", {min: 0.01, max: 1, step: 0.01, label: "Wobble Speed"});
            colorFolder.addInput(settings, "wobbleRange", {min: 1, max: 100, step: 1, label: "Wobble Range"});
    
            let motionFolder = gui.addFolder({title: "Motion", expanded: false});
            let perlinFolder = motionFolder.addFolder({title: "Perlin Walk", expanded: false});
            perlinFolder.addInput(settings, "perlinWalk", {label: "Perlin Walk"}).on("change", (ev) => {
                if(!ev.value) {
                    shoulderJoint.pointA.x = settings.originX;
                    shoulderJoint.pointA.y = settings.originY;
                }
            });
            perlinFolder.addInput(settings, "perlinFactor", {min: 0.001, max: 0.1, step: 0.0001, label: "Perlin Factor"});
            
            let followFolder = motionFolder.addFolder({title: "Follow Mouse", expanded: false});
            followFolder.addInput(settings, "followMouse", {label: "Follow Mouse"}).on("change", (ev) => {
                if(!ev.value) {
                    shoulderJoint.pointA.x = settings.originX;
                    shoulderJoint.pointA.y = settings.originY;
                }
            });

            let modulationFolder = motionFolder.addFolder({title: "Modulation", expanded: false});
            modulationFolder.addInput(settings, "modulateOrigin", {label: "Modulate Arm Origin"}).on("change", (ev) => {
                if(!ev.value) {
                    shoulderJoint.pointA.x = settings.originX;
                }
            });
            modulationFolder.addInput(settings, "modulateOriginXFactor", {min: 0, max: 1, step: 0.01, label: "Modulate X Amount"});
            modulationFolder.addInput(settings, "modulateOriginYFactor", {min: 0, max: 1, step: 0.01, label: "Modulate Y Amount"});
            modulationFolder.addInput(settings, "modulationXSpeed", {min: 0.1, max: 1.5, step: 0.01, label: "Modulate X Speed"});
            modulationFolder.addInput(settings, "modulationYSpeed", {min: 0.1, max: 1.5, step: 0.01, label: "Modulate Y Speed"});
            modulationFolder.addInput(settings, "randomForceFactor", {min: 0, max: 0.02, step: 0.001, label: "Random Force Amount"});
            modulationFolder.addInput(settings, "simulationSpeed", {min: 0.01, max: 1, step: 0.01, label: "Simulation Speed"}).on("change", (ev) => {
                engine.timing.timeScale = ev.value;
            });
    
            // // let captureFolder = gui.addFolder("Capture");
            // // captureFolder.addInput(settings, "captureFramerate", 1, 60, 1).name("Capture Framerate").on("change", (ev) => {
            // //     settings.capturing = false;
            // //     if (mediaRecorder && mediaRecorder.state && mediaRecorder.state === "recording") mediaRecorder.stop();
            // //     framesBetweenCaptures = 60 / settings.captureFramerate;
            // // });
            // // captureFolder.addInput(settings, "framesBetweenDraws", 0, 100, 1).name("Frames Between Draws");
            // // captureFolder.addInput(settings, "capturing", false, true).name("Capture").on("change", (ev) => {
            // //     if (settings.capturing) {
            // //         if (settings.captureFramerate > 10) {
            // //             if (mediaRecorder && mediaRecorder.state && mediaRecorder.state === "recording") mediaRecorder.stop();
            // //             mediaRecorder = new MediaRecorder(p.canvas.captureStream(settings.captureFramerate));
                        
            // //             mediaRecorder.ondataavailable = function(e) {
            // //                 chunks.push(e.data);
            // //             };
                        
            // //             mediaRecorder.onstop = function(e) {
            // //                 var blob = new Blob(chunks, { 'type' : 'video/mp4' }); // other types are available such as 'video/webm' for instance, see the doc for more info
            // //                 chunks = [];
            // //                 let video = document.createElement("video");
            // //                 var videoURL = URL.createObjectURL(blob);
            // //                 video.src = videoURL;
            // //                 video.controls = true;
            // //                 video.style.position = "fixed";
            // //                 document.body.appendChild(video);
            // //             };
            // //             mediaRecorder.start();
            // //         }
            // //     } else {
            // //         mediaRecorder.stop();
            // //     }
            // // });
            // // framesBetweenCaptures = 60 / settings.captureFramerate;

            let maskFolder = gui.addFolder({title: "Mask", expanded: false});
            let imageFolder = maskFolder.addFolder({title: "Image", expanded: false});
            imageFolder.addInput(settings, "useImageMask", {label: "Use Image Mask"});

            let textFolder = maskFolder.addFolder({title: "Text", expanded: false});
            textFolder.addInput(settings, "useTextMask", {label: "Use Text Mask"});
            textFolder.addInput(settings, "textMask", {label: "Text"});
            textFolder.addBlade({
                view: "list",
                label: "Font",
                options: [
                    {text: "East Sea Dokdo", value: "East Sea Dokdo"},
                    {text: "Oswald", value: "Oswald"},
                    {text: "Pirata One", value: "Pirata One"},
                    {text: "Playfair Display Italic", value: "Playfair Display Italic"},
                    {text: "Playfair Display", value: "Playfair Display"},
                ],
                value: "Oswald"
            }).on("change", ev => {
                settings.font = ev.value;
            });
            textFolder.addInput(settings, "fontSize", {min: 5, max: 1500, step:1, label: "Font Size"});

            gui.addInput(settings, "continousDrawing", {label: "Always Paint"});

            gui.addButton({title: "Load New Source Image"}).on("click", loadNewImage);
            gui.addButton({title: "Clear Canvas"}).on("click", clearCanvas);
            gui.addButton({title: "Randomise Brush Settings"}).on("click", randomBrush);
            gui.addButton({title: "Save"}).on("click", save);

            p.clear();
            p.background([settings.background.r, settings.background.g, settings.background.b, settings.background.a * 255]);

        }
        populateSettings();

        // draggin the menu
        let dragStartX, dragStartY;
        menu.ondrag = e => {
            menu.style.left = (e.clientX - dragStartX) + "px";
            menu.style.top = (e.clientY - dragStartY) + "px";
        };
        menu.ondragstart = e => {
            dragStartX = e.clientX - parseInt(menu.style.left || 0);
            dragStartY = e.clientY - parseInt(menu.style.top || 0);
        };
        menu.ondragend = e => {
            menu.style.left = (e.clientX - dragStartX) + "px";
            menu.style.top = (e.clientY - dragStartY) + "px";
            dragStartX = 0;
            dragStartY = 0;
        };

        // collapsing the menu
        document.getElementById("menubar").onpointerup = e => {
            if (menu.style.height === "fit-content") {
                menu.style.height = "33px";
            } else {
                menu.style.height = "fit-content";
            }
        }

        //Setup matter.js arm
        function setupMatterJS() {

            engine = Engine.create({enableSleeping: true});
            let world = engine.world;
            let render = Render.create({
                    element: document.querySelector(".arm"),
                    engine: engine,
                    options: {
                        width: window.innerWidth,
                        height: window.innerHeight
                    }
                });
            Render.run(render);
            let runner = Runner.create();
            Runner.run(runner, engine);
    
            resetArm = function() {
                if (arm) {
                    World.remove(world, arm);
                }
                let group = Body.nextGroup(true);
                arm = Composites.stack(settings.originX, settings.originY, settings.jointsOnArm, 1, 1, 1, function(x, y) {
                    return Bodies.rectangle(x, y, 50, 15, { collisionFilter: { group: group }, render: {strokeStyle: "#ff0000"} });
                });
                Composites.chain(arm, 0.5, 0, -0.5, 0, { stiffness: settings.armStiffness, length: settings.jointLength, render: { type: 'line', visible: true, strokeStyle: "#ff0000" } });
                shoulderJoint = Constraint.create({ 
                    bodyB: arm.bodies[0],
                    //pointB: { x: -25, y: 0 },
                    pointA: { x: arm.bodies[0].position.x, y: arm.bodies[0].position.y },
                    stiffness: settings.armStiffness,
                    render: { type: 'pin' }
                });
                window.arm = shoulderJoint;
                Composite.add(arm, shoulderJoint);
                Body.setDensity(arm.bodies[arm.bodies.length - 1], settings.endJointDensity);
                World.add(world, arm);
            }
    
            resetArm();
    
            World.add(world, [
                Bodies.rectangle(p.width / 2, -500, p.width * 2, 1000, { isStatic: true, density:100000 , friction: 1}), //top
                Bodies.rectangle(p.width + 500, p.height / 2, 1000, p.height, { isStatic: true, density:100000 , friction: 1}), //right
                Bodies.rectangle(p.width / 2, p.height + 500, p.width * 2, 1000, { isStatic: true, density:100000 , friction: 1}), //bottom
                Bodies.rectangle(-500, p.height / 2, 1000, p.height, { isStatic: true, density:100000 , friction: 1}) //left
            ]);
    
            // add mouse control
            let mouse = Mouse.create(render.canvas),
                mouseConstraint = MouseConstraint.create(engine, {
                    mouse: mouse,
                    constraint: {
                        stiffness: 0.2,
                        render: {
                            visible: false
                        }
                    }
                });
            World.add(world, mouseConstraint);
            // keep the mouse in sync with rendering
            render.mouse = mouse;
    
            // fit the render viewport to the scene
            Render.lookAt(render, {
                min: { x: 0, y: 0 },
                max: { x: window.innerWidth, y: window.innerHeight }
            });
        }
        setupMatterJS();
    }

    p.draw = function() {

        if (settings.followMouse) {
            shoulderJoint.pointA.x = p.mouseX;
            shoulderJoint.pointA.y = p.mouseY;
        } else if (settings.modulateOrigin) {
            shoulderJoint.pointA.x = (p.width / 2 * settings.modulateOriginXFactor) * Math.sin((90 + (p.frameCount / (10 / settings.modulationXSpeed))) % 360) + settings.originX;
            shoulderJoint.pointA.y = (p.height / 2 * settings.modulateOriginYFactor) * Math.cos((90 + (p.frameCount / (10 / settings.modulationYSpeed))) % 360) + settings.originY;
        } else if (settings.perlinWalk) {
            perlinIndexX += settings.perlinFactor;
            perlinIndexY += settings.perlinFactor * 1.1;
            shoulderJoint.pointA.x = p.noise(perlinIndexX) * p.width;
            shoulderJoint.pointA.y = p.noise(perlinIndexY) * p.height;
        }

        // if(settings.framesBetweenDraws) {
        //     if(framesSinceLastDraw === settings.framesBetweenDraws) {
        //         framesSinceLastDraw = 0;
        //     } else {
        //         framesSinceLastDraw++;
        //         return;
        //     }
        // }
        
        if(settings.fade) {
            p.fill(p.color([settings.background.r, settings.background.g, settings.background.b, (settings.background.a * 255) / settings.fadeAmount]));
            p.rect(0, 0, p.width, p.height);
        }

        if (p.keyIsDown(32) || settings.continousDrawing || (settings.followMouse && p.mouseIsPressed)) {
            masked = false;

            if (settings.modulateBrushSize) {
                settings.brushSize = Math.abs(_brushSize * Math.sin((p.frameCount / (10 / settings.modulationSpeed)) % 360));
            }
            
            for(let i = 0; i < arm.bodies.length; i++) {

                x = Math.floor(arm.bodies[i].position.x);
                y = Math.floor(arm.bodies[i].position.y);
                sampleX = Math.floor(arm.bodies[i].position.x * sizeRatio);
                sampleY = Math.floor(arm.bodies[i].position.y * sizeRatio);

                let pixelIndex = (sampleY * bgImg.width * 4) + sampleX * 4;

                if (settings.singleColor) {
                    //TODO: memoize this
                    if (settings.colorWobble) {
                        color = p.color(
                            Math.abs(settings.selectedColor.r + settings.wobbleRange * Math.sin((p.frameCount / (30 / settings.wobbleSpeed)) % 255)),
                            Math.abs(settings.selectedColor.g + settings.wobbleRange * Math.sin((p.frameCount / (20 / settings.wobbleSpeed)) % 255)),
                            Math.abs(settings.selectedColor.b + settings.wobbleRange * Math.sin((p.frameCount / (10 / settings.wobbleSpeed)) % 255)),
                            settings.brushAlpha
                        );
                    }
                    else {
                        color = p.color(settings.selectedColor.r, settings.selectedColor.g, settings.selectedColor.b, settings.brushAlpha);
                    }
                } else if (bgImg.pixels[pixelIndex] && bgImg.pixels[pixelIndex + 1] && bgImg.pixels[pixelIndex + 2]) {
                    if (sampleX > bgImg.width || sampleY > bgImg.height) {
                        color = p.color(
                            settings.background.r, settings.background.g, settings.background.b, settings.brushAlpha);
                    } else if(settings.enableTint) {
                        color = p.color(
                            (bgImg.pixels[pixelIndex] + settings.tint.r) / 2, 
                            (bgImg.pixels[pixelIndex + 1] + settings.tint.g) / 2, 
                            (bgImg.pixels[pixelIndex + 2] + settings.tint.b) / 2, 
                            settings.brushAlpha
                        );
                    } else {
                        color = p.color(
                            bgImg.pixels[pixelIndex], 
                            bgImg.pixels[pixelIndex + 1], 
                            bgImg.pixels[pixelIndex + 2], 
                            settings.brushAlpha
                        );
                    }
                }
                p.strokeWeight(0);
                p.fill(color);
                p.translate(x, y);
                if (settings.rotateBrush) {
                    p.rotate((p.PI * 2) * Math.random());
                }

                switch (settings.brushType) {
                    case 0: //circle
                        p.circle(0, 0, settings.brushSize);
                        break;
                    case 1: //square
                        p.rect(-(settings.brushSize / 2), -(settings.brushSize / 2), settings.brushSize, settings.brushSize);
                        break;
                    case 2: //rounded square
                        p.rect(-(settings.brushSize / 2), -(settings.brushSize / 2), settings.brushSize, settings.brushSize, settings.brushSize / 4);
                        break;
                    case 3: //thick rectangle
                        p.rect(-(settings.brushSize / 2), -(settings.brushSize / 4), settings.brushSize, settings.brushSize / 2);
                        break;
                    case 4: //thing rectangle
                        p.rect(-(settings.brushSize / 2), -(settings.brushSize / 8), settings.brushSize, settings.brushSize / 4);
                        break;
                    case 5: //triangle
                        p.triangle(
                            0, -(settings.brushSize / 2),
                            settings.brushSize / 2, settings.brushSize / 2,
                            -(settings.brushSize / 2), settings.brushSize / 2
                        );
                        break;
                    case 6: //random shape
                        let topRightX = (Math.random() * (settings.brushSize * 2)) - settings.brushSize;
                        let topRightY = (Math.random() * (settings.brushSize * 2)) - settings.brushSize;
                        let bottomRight = (Math.random() * (settings.brushSize * 2)) - settings.brushSize;
                        let bottomLeftX = (Math.random() * (settings.brushSize * 2)) - settings.brushSize;
                        let bottomLeftY = (Math.random() * (settings.brushSize * 2)) - settings.brushSize;
                        p.quad(
                            0, 0, 
                            topRightX, topRightY, 
                            topRightX, bottomRight, 
                            topRightX + bottomLeftX, bottomRight + bottomLeftY
                        );
                        break;
                    case 7: //lines
                        p.strokeWeight(settings.brushSize);
                        p.stroke(color)
                        p.line(0,0,Math.random() * settings.brushSize, Math.random() * settings.brushSize);
                        break;
                    case 8: //connected lines
                        if(previousBrushLocation[i]) {
                            // p.strokeCap(p.SQUARE)
                            p.strokeWeight(settings.brushSize);
                            p.stroke(color)

                            //experiment with the anchor points!
                            p.bezier(0, 0, 10, 10, previousBrushLocation[i][0], previousBrushLocation[i][1], previousBrushLocation[i][0] - x, previousBrushLocation[i][1] - y);
                        }
                        previousBrushLocation[i] = [x, y];
                        break;
                }
                p.resetMatrix();
                
                
            }

            if (previousFrame && settings.capturing && settings.captureFramerate <= 10) {
                if(p.frameCount - previousFrame >= framesBetweenCaptures) {
                    frame++;
                    p.saveCanvas("artarm_" + seed + "/output_" + frame, "png");
                    previousFrame = p.frameCount;
                }
            } else if(settings.capturing && settings.captureFramerate <= 10) {
                frame++;
                p.saveCanvas("artarm_" + seed + "/output_" + frame, "png");
                previousFrame = p.frameCount;
            }
        } else {
            previousBrushLocation = [];
            if (!masked && settings.useTextMask) {
                masked = true;
                let temp = p.createGraphics(p.width, p.height);
                temp.fill(0, 255, 0, 0);
                temp.rect(0, 0, p.width, p.height)
                temp.textSize(settings.fontSize);
                temp.textAlign(p.CENTER, p.CENTER);
                temp.textFont(fonts[settings.font]);
                temp.fill(0, 255, 255);
                temp.text(settings.textMask, p.width / 2, p.height / 2);
    
                let canvasSnapshot = p.get();
                canvasSnapshot.mask(temp);
                p.clear();
                p.background([settings.background.r, settings.background.g, settings.background.b, settings.background.a * 255]);
                p.image(canvasSnapshot.get(), 0, 0);
            } else if(!masked && settings.useImageMask) {
                masked = true;
                p.loadImage("assets/nk_shield_black.png", img => {
                    let temp = p.createGraphics(p.width, p.height);
                    let maskWidthRatio = img.width / p.width;
                    let maskHeightRatio = img.height / p.height;
                    let maskSizeRatio = Math.max(maskWidthRatio, maskHeightRatio);
                    if (maskSizeRatio > 1) {
                        temp.image(img, p.width / 2 - (img.width / maskSizeRatio) / 2, p.height / 2 - (img.height / maskSizeRatio) / 2, img.width / maskSizeRatio, img.height / maskSizeRatio);
                    } else {
                        temp.image(img, p.width / 2 - img.width / 2, p.height / 2 - img.height / 2, img.width, img.height);
                    }
                    let canvasSnapshot = p.get();
                    canvasSnapshot.mask(temp);
                    p.clear();
                    p.background([settings.background.r, settings.background.g, settings.background.b, settings.background.a * 255]);
                    p.image(canvasSnapshot.get(), 0, 0);
                })
    
            }
        }

        Body.applyForce(
            arm.bodies[arm.bodies.length - 1], 
            {
                x: Math.random() * 2 - 1, 
                y: Math.random() * 2 - 1
            }, 
            {
                x: (Math.random() * (settings.randomForceFactor) * 2) - settings.randomForceFactor, 
                y: (Math.random() * (settings.randomForceFactor) * 2) - settings.randomForceFactor
            }
        );
    }

    p.keyPressed = function() {
        switch(p.keyCode) {
            case p.DOWN_ARROW:
                save();
                break;
            case p.ESCAPE:
                clearCanvas();
                break;
            case p.TAB:
                randomBrush();
                break;
        }
    } 

    p.save = function() {
        p.saveCanvas("generative/output" + p.random(0,155000), "png");
    }
}